же в помещениях, где могут скапливаться взрывоопасные газы. Степень защиты корпуса двигателя от внешних воздействий – IP54/ IP55/ IP67.")]),_c('p',[_vm._v("Электродвигатели АВРМ предназначены для продолжительной работы от сети переменного тока с напряжением 1140/660/380 Вольт и частотой тока 50 Гц. Класс энергоэффективности агрегатов – IE1 / IE2, климатическое исполнение – У2 / У5. Номинальный режим функционирования электродвигателя – S1, частота вращения – 1500 об/мин. или 3000 об/мин.")]),_c('p',{staticClass:"second"},[_vm._v("Оснащение двигателей АВРМ «Элдвиг СПб» и дополнительные опции:")]),_c('ul',[_c('li',[_vm._v("вариативное исполнение конца вала;")]),_c('li',[_vm._v("вариативное оснащение узлов подшипников;")]),_c('li',[_vm._v("импортозамещение зарубежных аналогов;")]),_c('li',[_vm._v("фабричная окраска в колер заказчика.")])]),_c('p',[_vm._v("Средний срок эксплуатации электродвигателей серии АВРМ производства «Элдвиг СПб» составляет 15 лет. Это надежное и эффективное оборудование, удовлетворяющее всем современным требованиям.")]),_c('h3',{staticClass:"first"},[_vm._v("Взрывобезопасные электродвигатели АВРМ: достоинства и сферы использования")]),_c('p',[_vm._v("Двигатели серии АВРМ используются в приводах вентиляторов местного проветривания. Взрывозащищенный корпус позволяет использовать агрегаты в рудниках, угольных и соляных шахтах, где имеется опасность по газу метану и/или угольной пыли, в том числе и на тупиковых выработках.")]),_c('p',{staticClass:"second"},[_vm._v("Достоинства электродвигателей АВРМ «Элдвиг СПб»:")]),_c('ul',[_c('li',[_vm._v("применение только высококачественных намоточных, изоляционных и пропиточных материалов гарантирует безотказную работу агрегатов в течение 30 тыс. часов в режиме S1;")]),_c('li',[_vm._v("использование динамной электротехнической стали М530-А50, из которой выполнен корпус электродвигателя, позволяет снизить энергопотери и поднять класс энергоэффективности агрегатов до IE1;")]),_c('li',[_vm._v("используемые в базовой комплектации подшипники SKF являются «необслуживаемыми», т.е. не требуют дополнительной смазки на весь гарантийный срок эксплуатации (30 тыс. мото/часов). Это гарантирует качественную работу подшипникового узла при повышенных нагрузках. Так же возможна установка подшипников марки TIMKEN или использование подшипников других марок по требованию клиента;")]),_c('li',[_vm._v("продуманная конструкция корпусов с оребрением и удлиненным кожухом обеспечивает охлаждение электродвигателя даже при повышенных нагрузках. Особого внимания заслуживают ребра, которые привариваются на роботизированном комплексе, что обеспечивает их идеальное сочленение с корпусом электродвигателя;")]),_c('li',[_vm._v("изоляционный материал класса H способен выдерживать нагрев двигателя до 200 градусов по Цельсию;")]),_c('li',[_vm._v("всыпная обмотка укладывается ручным способом, что позволяет избежать повышения холостых токов и сохранить тем самым КПД оборудования и его энергоэффективность.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-md-3 col-sm-3 col-xs-12 char"},[_c('img',{attrs:{"src":require("./img/item.png")}}),_c('h6',{staticClass:"name"},[_vm._v("АВРМ-132")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{attrs:{"id":"chertiz"}},[_c('span',[_vm._v("Чертеж")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',[_c('b',[_vm._v("Дополнительные опции и оснащение")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('ul',[_c('li',[_vm._v("вариантное исполнение конца вала;")]),_c('li',[_vm._v("вариантное исполнение подшипниковых узлов;")]),_c('li',[_vm._v("вариантное монтажное исполнение: фланцевое, лаповое, комбинированное;")]),_c('li',[_vm._v("исполнение в замену любого иностранного аналога;")]),_c('li',[_vm._v("заводская окраска в колер клиента.")])])},function () {var _vm=this;var _h=_vm